"use strict";
import Vue      from 'vue';
import Router   from 'vue-router';

const index = r => require.ensure([], () => r(require('../components/index.vue')), 'index');
const blog = r => require.ensure([], () => r(require('../components/blog/blog.vue')), 'blog');
const searchResult = r => require.ensure([], () => r(require('../components/blog/searchResult.vue')), 'searchResult');
const articleDetail = r => require.ensure([], () => r(require('../components/blog/articleDetail.vue')), 'articleDetail');
const author = r => require.ensure([], () => r(require('../components/author.vue')), 'author');
const photoWall = r => require.ensure([], () => r(require('../components/photoWall.vue')), 'photoWall');
const photoList = r => require.ensure([], () => r(require('../components/photoList.vue')), 'photoList');
// 
const prod = r => require.ensure([], () => r(require('../components/prod.vue')), 'prod');
const error = r => require.ensure([], () => r(require('../components/404.vue')), 'error');

Vue.use(Router);

export default new Router({
	mode: 'history',
	routes: [
	    { path: "/", component: index, name: "index", meta: {title: ""}},
	    { path: "/blog", component: blog, name: "blog", meta: {title: ""}},
	    { path: "/searchResult", component: searchResult, name: "blog_searchResult", meta: {title: ""}},
	    { path: "/articleDetail", component: articleDetail, name: "blog_articleDetail", meta: {title: ""}},
	    { path: "/photoWall", component: photoWall, name: "photoWall", meta: {title: ""}},
	    { path: "/photoList", component: photoList, name: "photoWall_list", meta: {title: ""}},
	    { path: "/author", component: author, name: "author", meta: {title: ""}},
	    { path: "/prod", component: prod, name: "prod", meta: {title: ""}},
	    { path: "*", component: error, name: "404", meta: {title: "404"}}
    ],
    scrollBehavior (to, from, savedPosition) {
	    return {x: 0,y: 0}
	}
});


